// Fonction utilitaire pour normaliser les noms de catgories
export const normalizeCategory = (category) => {
  if (!category) return "";

  // Convertir en chane de caractres
  const categoryStr = String(category);

  // Supprimer les espaces, mettre en majuscules et supprimer les accents
  return categoryStr
    .trim()
    .toUpperCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, ""); // Supprimer les diacritiques (accents, etc.)
};

// Fonction pour extraire le poids d'une catgorie de poids
export const extractWeight = (weightCategory) => {
  // S'il s'agit d'un objet avec une proprit 'name'
  const weightStr =
    typeof weightCategory === "object" && weightCategory.name
      ? weightCategory.name
      : weightCategory;

  // Extraire le nombre du poids (ex: "-58kg" -> 58, "+87kg" -> 87)
  const match = weightStr.match(/([+-])(\d+)kg/);
  if (!match) return null;

  const isPlus = match[1] === "+";
  const weight = parseInt(match[2], 10);

  // Pour une catgorie avec +, nous retournons le poids + 0.1 pour qu'il soit suprieur  la limite exacte
  return isPlus ? weight + 0.1 : weight;
};

// Fonction utilitaire pour trouver les informations PSS pour une catgorie d'ge, un genre et un poids
export const findPssInfo = (ageCategory, gender, weightCategory) => {
  try {
    console.log("findPssInfo appele avec:", {
      ageCategory,
      gender,
      weightCategory,
    });

    if (!ageCategory || !gender || !weightCategory) {
      console.log("Paramtres manquants pour findPssInfo");
      return null;
    }

    // Tableau de correspondance pour les noms de catgories d'ge normaliss
    const ageCategoryMapping = {
      baby: "BABY",
      babys: "BABY",
      pupille: "PUPILLE",
      pupilles: "PUPILLE",
      benjamin: "BENJAMIN",
      benjamins: "BENJAMIN",
      benjamine: "BENJAMIN",
      benjamines: "BENJAMIN",
      minime: "MINIME",
      minimes: "MINIME",
      cadet: "CADET",
      cadets: "CADET",
      cadette: "CADET",
      cadettes: "CADET",
      junior: "JUNIOR",
      juniors: "JUNIOR",
      espoir: "ESPOIR",
      espoirs: "ESPOIR",
      senior: "SENIOR",
      seniors: "SENIOR",
      master: "MASTER",
      masters: "MASTER",
      veteran: "MASTER",
      veterans: "MASTER",
    };

    // Normaliser la catgorie d'ge
    const normalizedAgeCategory = normalizeCategory(ageCategory);

    // Essayer de trouver la cl directement via le mapping
    let ageCatKey = ageCategoryMapping[normalizedAgeCategory];

    // Si pas trouv dans le mapping, essayer de manire standard
    if (!ageCatKey) {
      ageCatKey = Object.keys(AGE_CATEGORIES).find(
        (key) =>
          normalizeCategory(AGE_CATEGORIES[key].name) === normalizedAgeCategory
      );
    }

    console.log("Cl de catgorie d'ge trouve:", ageCatKey);

    if (!ageCatKey) return null;

    // Normaliser le genre
    const genderNormalized = gender.toString().trim().toUpperCase();
    const genderKey =
      genderNormalized === "FEMALE" || genderNormalized === "F"
        ? "FEMALE"
        : "MALE";

    // Obtenir la liste des catgories de poids
    const weightCategories = KYORUGI_CATEGORIES[genderKey][ageCatKey];
    console.log("Catgories de poids disponibles:", weightCategories);

    if (!weightCategories) {
      console.log("Aucune catgorie de poids trouve pour cette combinaison");
      return null;
    }

    // Extraire le nom de la catgorie de poids de diffrentes manires possibles
    let weightName;

    if (typeof weightCategory === "string") {
      // Si c'est dj une chane simple
      weightName = weightCategory;
    } else if (typeof weightCategory === "object") {
      // Si c'est un objet, essayer diffrentes proprits
      if (weightCategory.name) {
        weightName = weightCategory.name;
      } else if (weightCategory.weightCategoryName) {
        weightName = weightCategory.weightCategoryName;
      } else if (weightCategory.value) {
        weightName = weightCategory.value;
      }
    }

    console.log("Nom de catgorie de poids extrait:", weightName);

    // Si aucun nom n'a pu tre extrait, quitter
    if (!weightName) {
      console.log("Impossible d'extraire un nom de catgorie de poids");
      return null;
    }

    // Nettoyer les espaces et normaliser
    weightName = weightName.toString().trim();

    // Trouver la catgorie de poids correspondante
    const matchingWeightCat = weightCategories.find((wc) => {
      // Extraire le nom selon le type
      const wcName = typeof wc === "object" ? wc.name : wc;

      // Comparer les noms de catgorie
      if (wcName === weightName) {
        return true;
      }

      // Normaliser pour la comparaison
      const cleanedName = weightName.replace(/\s+/g, "").toLowerCase();
      const cleanedWcName = wcName.replace(/\s+/g, "").toLowerCase();

      // Comparaison sensible aux signes + et -
      if (cleanedName === cleanedWcName) {
        return true;
      }

      // Comparaison en ignorant "kg"  la fin
      const nameWithoutUnit = cleanedName.replace(/kg$/, "");
      const wcNameWithoutUnit = cleanedWcName.replace(/kg$/, "");

      return nameWithoutUnit === wcNameWithoutUnit;
    });

    console.log(
      "Catgorie de poids correspondante trouve:",
      matchingWeightCat
    );

    if (!matchingWeightCat) return null;

    // Si c'est dj un objet avec pss et hitLevel, le retourner directement
    if (
      typeof matchingWeightCat === "object" &&
      matchingWeightCat.pss &&
      matchingWeightCat.hitLevel
    ) {
      console.log("Informations PSS trouves:", {
        pss: matchingWeightCat.pss,
        hitLevel: matchingWeightCat.hitLevel,
      });
      return {
        pss: matchingWeightCat.pss,
        hitLevel: matchingWeightCat.hitLevel,
      };
    }

    // Sinon, retourner null car nous n'avons pas d'informations PSS pour cette catgorie
    console.log("Aucune information PSS trouve");
    return null;
  } catch (error) {
    console.error("Erreur lors de la recherche des informations PSS:", error);
    return null;
  }
};
