import React, { useEffect, useState } from "react";
import { useCompetition } from "../context/CompetitionContext";
import {
  checkExistingGroupsAndPools,
  deleteAllGroups,
  fetchFormattedGroupsAndPools,
  saveGroupsAndPools,
} from "../services/dbService";
import "../styles/GroupsDisplay.css";
import { createGroups } from "../utils/groupManager";

const GroupDisplay = ({
  participants,
  tournamentConfig,
  setGroups,
  nextStep,
  prevStep,
  groups,
}) => {
  const { competitionId } = useCompetition();
  const [generatedGroups, setGeneratedGroups] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [groupStats, setGroupStats] = useState({
    totalGroups: 0,
    totalPools: 0,
    totalParticipants: participants.length,
    unusedParticipants: 0,
  });
  const [savingGroups, setSavingGroups] = useState(false);
  const [saveSuccess, setSaveSuccess] = useState(false);
  const [dataSource, setDataSource] = useState(""); // 'new' ou 'existing'
  const [deletingGroups, setDeletingGroups] = useState(false);
  const [deleteSuccess, setDeleteSuccess] = useState(false);

  useEffect(() => {
    if (participants.length > 0 && tournamentConfig && competitionId) {
      // Si des groupes valides ont dj t chargs dans les props, les utiliser directement
      if (generatedGroups.length === 0 && groups && groups.length > 0) {
        console.log(
          "Utilisation des groupes fournis par les props:",
          groups.length
        );
        setGeneratedGroups(groups);
        updateStats(groups);
        setIsLoading(false);
        setDataSource("existing");
      } else {
        // Sinon, chercher dans la base de donnes ou gnrer
        loadOrGenerateGroups();
      }
    }
  }, [participants, tournamentConfig, competitionId, groups]);

  // Nouvelle fonction pour vrifier l'existence des groupes et charger ou gnrer selon le cas
  const loadOrGenerateGroups = async () => {
    try {
      setIsLoading(true);
      setError(null);

      // Vrifier si des groupes existent dj pour cette comptition
      const existingGroupsResult = await checkExistingGroupsAndPools(
        competitionId
      );

      if (existingGroupsResult.exists && existingGroupsResult.count > 0) {
        console.log(
          `${existingGroupsResult.count} groupes existants trouvs, chargement des donnes...`
        );
        setDataSource("existing");

        // Charger les groupes existants
        await loadExistingGroups();
      } else {
        console.log(
          "Aucun groupe existant trouv, gnration de nouveaux groupes..."
        );
        setDataSource("new");

        // Gnrer de nouveaux groupes
        generateNewGroups();
      }
    } catch (error) {
      console.error(
        "Erreur lors de la vrification/chargement des groupes:",
        error
      );
      setError(`Erreur lors du chargement des donnes: ${error.message}`);
      setIsLoading(false);
    }
  };

  // Nouvelle fonction pour charger les groupes existants
  const loadExistingGroups = async () => {
    try {
      const loadedGroups = await fetchFormattedGroupsAndPools(competitionId);

      console.log(
        `${loadedGroups.length} groupes chargs depuis la base de donnes`
      );

      // Dduplication: S'assurer que chaque groupe est unique par combinaison de genre/ge/poids
      const uniqueGroups = deduplicateGroups(loadedGroups);

      console.log(
        `Aprs dduplication: ${uniqueGroups.length} groupes uniques (${
          loadedGroups.length - uniqueGroups.length
        } doublons limins)`
      );

      // Ajouter du dbogage pour examiner la structure des groupes chargs
      if (uniqueGroups.length > 0) {
        const firstGroup = uniqueGroups[0];
        console.log("Premier groupe charg:", {
          id: firstGroup.id,
          gender: firstGroup.gender,
          ageCategory: firstGroup.ageCategory,
          weightCategory: firstGroup.weightCategory,
          participantsCount: firstGroup.participants
            ? firstGroup.participants.length
            : 0,
          poolsCount: firstGroup.pools ? firstGroup.pools.length : 0,
        });

        // Vrifier la structure des participants dans le groupe
        if (firstGroup.participants && firstGroup.participants.length > 0) {
          console.log(
            "Premier participant dans le groupe:",
            firstGroup.participants[0]
          );
        }

        // Vrifier la structure des poules
        if (firstGroup.pools && firstGroup.pools.length > 0) {
          console.log("Premire poule du groupe:", firstGroup.pools[0]);

          // Vrifier les IDs dans la poule
          const poolParticipantIds = firstGroup.pools[0];
          console.log(
            "IDs des participants dans la premire poule:",
            poolParticipantIds
          );

          // Vrifier si les IDs correspondent aux participants
          if (firstGroup.participants && firstGroup.participants.length > 0) {
            const foundParticipants = poolParticipantIds.map((id) =>
              firstGroup.participants.find((p) => p.id === id)
            );
            console.log(
              "Participants trouvs dans la poule:",
              foundParticipants.filter(Boolean).length
            );
            console.log(
              "Participants non trouvs dans la poule:",
              foundParticipants.filter((p) => !p).length
            );
          }
        }
      }

      // Charger les informations compltes des participants si elles manquent
      // Pour chaque groupe, pour chaque poule, s'assurer que les participants dans le groupe sont complets
      const groupsWithCompletedParticipants = await Promise.all(
        uniqueGroups.map(async (group) => {
          // S'assurer que le groupe a des participants et qu'ils ont des informations compltes
          if (!group.participants || group.participants.length === 0) {
            console.log(
              `Groupe ${group.id} n'a pas de participants ou participants incomplets, tentative de rcupration`
            );

            // Collecter tous les IDs de participants dans les poules de ce groupe
            const participantIdsInPools = new Set();
            if (group.pools) {
              group.pools.forEach((pool) => {
                pool.forEach((participantId) => {
                  participantIdsInPools.add(participantId);
                });
              });
            }

            // Si le groupe a des participants incomplets, tenter de les complter
            if (participantIdsInPools.size > 0) {
              console.log(
                `Tentative de rcupration des informations pour ${participantIdsInPools.size} participants du groupe ${group.id}`
              );

              // Si certains IDs ne sont pas dans les participants du groupe, les rcuprer
              const participantsToFetch = [...participantIdsInPools].filter(
                (id) =>
                  !group.participants ||
                  !group.participants.some((p) => p.id === id)
              );

              if (participantsToFetch.length > 0) {
                console.log(
                  `${participantsToFetch.length} participants  rcuprer pour le groupe ${group.id}`
                );

                // Tenter de trouver les participants manquants dans la liste globale
                const foundInGlobalList = participantsToFetch
                  .map((id) => participants.find((p) => p.id === id))
                  .filter(Boolean);

                console.log(
                  `${foundInGlobalList.length} participants trouvs dans la liste globale`
                );

                // Mettre  jour le groupe avec les participants trouvs
                group.participants = [
                  ...(group.participants || []),
                  ...foundInGlobalList,
                ];
              }
            }
          }

          return group;
        })
      );

      setGeneratedGroups(groupsWithCompletedParticipants);

      // Calculer les statistiques
      let totalPools = 0;
      groupsWithCompletedParticipants.forEach((group) => {
        if (group.pools) {
          totalPools += group.pools.length;
        }
      });

      // Calculer les participants non utiliss
      const usedParticipantIds = new Set();
      groupsWithCompletedParticipants.forEach((group) => {
        group.pools.forEach((pool) => {
          pool.forEach((participantId) => {
            usedParticipantIds.add(participantId);
          });
        });
      });

      const unusedParticipants = participants.length - usedParticipantIds.size;

      setGroupStats({
        totalGroups: groupsWithCompletedParticipants.length,
        totalPools: totalPools,
        totalParticipants: participants.length,
        unusedParticipants: unusedParticipants,
      });

      setError(null);
      setIsLoading(false);
    } catch (error) {
      console.error("Erreur lors du chargement des groupes existants:", error);
      setError(`Erreur lors du chargement des groupes: ${error.message}`);
      setIsLoading(false);
    }
  };

  // Fonction pour ddupliquer les groupes base sur genre/catgorie d'ge/catgorie de poids
  const deduplicateGroups = (groups) => {
    // Map pour stocker les groupes uniques, base sur une cl composite
    const uniqueGroupsMap = new Map();

    groups.forEach((group) => {
      // Crer une cl composite pour identifier les groupes similaires
      const key = `${group.gender}-${group.ageCategory.name}-${group.weightCategory.name}`;

      if (!uniqueGroupsMap.has(key)) {
        // Premier groupe avec cette combinaison, on le garde
        uniqueGroupsMap.set(key, group);
      } else {
        // Groupe en double, on compare les dates de cration si possible
        const existingGroup = uniqueGroupsMap.get(key);

        // Si le groupe existant a moins de poules, on le remplace par le nouveau qui peut tre plus complet
        if (
          !existingGroup.pools ||
          (group.pools &&
            group.pools.length > (existingGroup.pools?.length || 0))
        ) {
          uniqueGroupsMap.set(key, group);
        }

        console.log(`Groupe en double dtect et filtr: ${key}`);
      }
    });

    // Convertir la Map en tableau
    return Array.from(uniqueGroupsMap.values());
  };

  // Fonction pour gnrer de nouveaux groupes
  const generateNewGroups = () => {
    try {
      // Gnrer les groupes  partir des participants et de la configuration
      const { groups, stats } = createGroups(participants, tournamentConfig);
      setGeneratedGroups(groups);
      setGroupStats({
        totalGroups: groups.length,
        totalPools: stats.totalPools,
        totalParticipants: participants.length,
        unusedParticipants: stats.unusedParticipants,
      });
      setError(null);
    } catch (err) {
      setError(`Erreur lors de la gnration des groupes: ${err.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  // Fonction pour continuer vers l'tape suivante
  const handleContinue = () => {
    // Mettre  jour l'tat global avec les groupes gnrs
    setGroups(generatedGroups);
    nextStep();
  };

  // Format du nom d'un groupe
  const formatGroupName = (group) => {
    return `${group.gender === "male" ? "H" : "F"} ${group.ageCategory.name} ${
      group.weightCategory.name
    }`;
  };

  // Fonction pour sauvegarder les groupes
  const handleSaveGroups = async () => {
    setSavingGroups(true);
    try {
      // Vrifier d'abord si des groupes similaires existent dj
      const existingGroupsResult = await checkExistingGroupsAndPools(
        competitionId
      );

      if (existingGroupsResult.exists && existingGroupsResult.count > 0) {
        // Demander confirmation avant de continuer
        const confirmResult = window.confirm(
          `${existingGroupsResult.count} groupes existent dj pour cette comptition. Voulez-vous vraiment sauvegarder  nouveau? Cela pourrait crer des doublons.`
        );

        if (!confirmResult) {
          console.log("Sauvegarde annule par l'utilisateur");
          setSavingGroups(false);
          return;
        }

        console.log("Sauvegarde confirme malgr les groupes existants");
      }

      // Continuer avec la sauvegarde
      const result = await saveGroupsAndPools(competitionId, generatedGroups);

      if (result.success) {
        console.log("Groupes sauvegards avec succs");
        setSaveSuccess(true);
        // Mettre  jour la source de donnes pour indiquer que les groupes sont maintenant enregistrs
        setDataSource("existing");
        // Masquer le message de succs aprs 3 secondes
        setTimeout(() => setSaveSuccess(false), 3000);
      } else {
        console.error(
          "Erreurs lors de la sauvegarde des groupes:",
          result.errors
        );
        alert(
          `${result.errors.length} erreurs sont survenues lors de la sauvegarde. Consultez la console pour plus de dtails.`
        );
      }
    } catch (error) {
      console.error("Erreur lors de la sauvegarde des groupes:", error);
      alert("Erreur lors de la sauvegarde des groupes. Veuillez ressayer.");
    } finally {
      setSavingGroups(false);
    }
  };

  // Fonction pour mettre  jour les statistiques  partir des groupes
  const updateStats = (groupsData) => {
    if (!Array.isArray(groupsData) || groupsData.length === 0) return;

    let totalPoolsCount = 0;
    let usedParticipants = new Set();

    groupsData.forEach((group) => {
      // Compter les poules
      if (group.pools && Array.isArray(group.pools)) {
        totalPoolsCount += group.pools.length;
      }

      // Compter les participants utiliss
      if (group.participants && Array.isArray(group.participants)) {
        group.participants.forEach((p) => {
          if (p && p.id) {
            usedParticipants.add(p.id);
          }
        });
      }
    });

    setGroupStats({
      totalGroups: groupsData.length,
      totalPools: totalPoolsCount,
      totalParticipants: participants.length,
      unusedParticipants: participants.length - usedParticipants.size,
    });
  };

  // Fonction pour supprimer tous les groupes existants
  const handleDeleteGroups = async () => {
    // Demander confirmation
    const confirmResult = window.confirm(
      "tes-vous sr de vouloir supprimer tous les groupes existants? Cette action est irrversible."
    );

    if (!confirmResult) {
      console.log("Suppression annule par l'utilisateur");
      return;
    }

    setDeletingGroups(true);
    try {
      const result = await deleteAllGroups(competitionId);

      if (result.success) {
        console.log("Groupes supprims avec succs:", result.message);
        setDeleteSuccess(true);
        // Mettre  jour la source de donnes
        setDataSource("new");
        // Gnrer de nouveaux groupes
        generateNewGroups();
        // Masquer le message de succs aprs 3 secondes
        setTimeout(() => setDeleteSuccess(false), 3000);
      } else {
        console.error(
          "Erreur lors de la suppression des groupes:",
          result.message
        );
        alert(`Erreur lors de la suppression des groupes: ${result.message}`);
      }
    } catch (error) {
      console.error("Erreur lors de la suppression des groupes:", error);
      alert("Erreur lors de la suppression des groupes. Veuillez ressayer.");
    } finally {
      setDeletingGroups(false);
    }
  };

  return (
    <div className="groups-display-container">
      <h2>tape 3: Rpartition des participants en groupes et poules</h2>

      {isLoading ? (
        <div className="loading">
          <p>Chargement des groupes en cours...</p>
        </div>
      ) : error ? (
        <div className="error-message">{error}</div>
      ) : (
        <>
          <div className="groups-stats">
            <h3>Rsum</h3>
            <p>Nombre total de groupes: {groupStats.totalGroups}</p>
            <p>Nombre total de poules: {groupStats.totalPools}</p>
            <p>
              Participants utiliss:{" "}
              {groupStats.totalParticipants - groupStats.unusedParticipants} sur{" "}
              {groupStats.totalParticipants}
            </p>
            {groupStats.unusedParticipants > 0 && (
              <p className="warning">
                Attention: {groupStats.unusedParticipants} participants n'ont
                pas pu tre placs dans des poules.
              </p>
            )}
            {dataSource === "existing" && (
              <p className="info">
                Des groupes existants ont t chargs depuis la base de donnes.
              </p>
            )}
          </div>

          <div className="action-buttons">
            <button
              className="save-btn"
              onClick={handleSaveGroups}
              disabled={
                isLoading ||
                generatedGroups.length === 0 ||
                savingGroups ||
                dataSource === "existing"
              }
            >
              {savingGroups
                ? "Sauvegarde en cours..."
                : dataSource === "existing"
                ? "Groupes dj sauvegards"
                : "Sauvegarder les groupes"}
            </button>

            {dataSource === "existing" && (
              <>
                <button
                  className="reload-btn"
                  onClick={() => loadOrGenerateGroups()}
                  disabled={isLoading}
                >
                  {isLoading ? "Chargement..." : "Actualiser les donnes"}
                </button>

                <button
                  className="delete-btn"
                  onClick={handleDeleteGroups}
                  disabled={isLoading || deletingGroups}
                >
                  {deletingGroups
                    ? "Suppression en cours..."
                    : "Supprimer les groupes"}
                </button>
              </>
            )}

            {saveSuccess && (
              <div className="success-message">
                Les groupes ont t sauvegards avec succs !
              </div>
            )}

            {deleteSuccess && (
              <div className="success-message">
                Les groupes ont t supprims avec succs !
              </div>
            )}
          </div>

          <div className="groups-list">
            <h3>Groupes et poules</h3>

            {generatedGroups.map((group, groupIndex) => (
              <div key={groupIndex} className="group-card">
                <h4 className="group-title">{formatGroupName(group)}</h4>
                <p>Nombre de participants: {group.participants.length}</p>
                <p>Nombre de poules: {group.pools.length}</p>

                <div className="pools-container">
                  {group.pools.map((pool, poolIndex) => (
                    <div key={poolIndex} className="pool-card">
                      <h5>Poule {poolIndex + 1}</h5>
                      <table className="participants-table">
                        <thead>
                          <tr>
                            <th>Nom</th>
                            <th>Prnom</th>
                            <th>Rgion</th>
                            <th>Club</th>
                          </tr>
                        </thead>
                        <tbody>
                          {pool.map((participantId, partIndex) => {
                            // Chercher d'abord dans les participants du groupe qui contient des objets complets
                            const participantInGroup =
                              group.participants &&
                              Array.isArray(group.participants)
                                ? group.participants.find(
                                    (p) => p.id === participantId
                                  )
                                : null;

                            // Si non trouv dans le groupe, chercher dans les participants globaux
                            const participant =
                              participantInGroup ||
                              participants.find((p) => p.id === participantId);

                            if (!participant) {
                              console.log(
                                `Participant avec ID ${participantId} non trouv`
                              );
                              return (
                                <tr key={partIndex}>
                                  <td colSpan="4">
                                    Participant ID: {participantId} (non trouv)
                                  </td>
                                </tr>
                              );
                            }

                            return (
                              <tr key={partIndex}>
                                <td>{participant.nom}</td>
                                <td>{participant.prenom}</td>
                                <td>{participant.ligue}</td>
                                <td>{participant.club || "-"}</td>
                              </tr>
                            );
                          })}
                        </tbody>
                      </table>
                    </div>
                  ))}
                </div>
              </div>
            ))}

            {generatedGroups.length === 0 && (
              <p className="no-groups">
                Aucun groupe n'a pu tre gnr avec les paramtres actuels.
              </p>
            )}
          </div>

          {groupStats.unusedParticipants > 0 && (
            <div className="unused-participants">
              <h3>Participants non placs</h3>
              <p>
                Les participants suivants n'ont pas pu tre placs dans des
                poules, gnralement parce qu'ils ne correspondent  aucune
                catgorie ou qu'il n'y a pas assez de participants dans leur
                catgorie pour former une poule complte.
              </p>

              <table className="participants-table">
                <thead>
                  <tr>
                    <th>Nom</th>
                    <th>Prnom</th>
                    <th>Sexe</th>
                    <th>ge</th>
                    <th>Poids</th>
                    <th>Rgion</th>
                    <th>Club</th>
                  </tr>
                </thead>
                <tbody>
                  {participants
                    .filter(
                      (p) =>
                        !generatedGroups.some((group) =>
                          group.pools.some((pool) => pool.includes(p.id))
                        )
                    )
                    .map((participant, index) => (
                      <tr key={index}>
                        <td>{participant.nom}</td>
                        <td>{participant.prenom}</td>
                        <td>{participant.sexe}</td>
                        <td>{participant.age}</td>
                        <td>{participant.poids}</td>
                        <td>{participant.ligue}</td>
                        <td>{participant.club || "-"}</td>
                      </tr>
                    ))}
                </tbody>
              </table>
            </div>
          )}
        </>
      )}

      <div className="navigation-buttons">
        <button className="prev-btn" onClick={prevStep} disabled={isLoading}>
          Prcdent
        </button>
        <button
          className="next-btn"
          onClick={handleContinue}
          disabled={
            isLoading ||
            (dataSource !== "existing" && generatedGroups.length === 0)
          }
        >
          Suivant
        </button>
      </div>
    </div>
  );
};

export default GroupDisplay;
