import React, { createContext, useContext, useState } from "react";
import { saveCompetitionState } from "../services/dbService";

const CompetitionContext = createContext();

export const CompetitionProvider = ({ children }) => {
  const [competitionId, setCompetitionId] = useState(null);
  const [competitionName, setCompetitionName] = useState(
    "Comptition de Taekwondo"
  );

  const initializeCompetition = async (config) => {
    try {
      console.log("Dbut de l'initialisation de la comptition");
      console.log("Configuration reue:", config);

      const competitionData = {
        id: competitionId,
        name: config.name || competitionName,
        date: new Date(),
        startTime: config.startTime || new Date(),
        roundDuration: config.roundDuration,
        breakDuration: config.breakDuration,
        breakFrequency: config.breakFrequency,
        poolSize: config.poolSize, // Ajout du poolSize
      };

      console.log("Donnes de la comptition  sauvegarder:", competitionData);
      console.log("ID de comptition existant:", competitionId);

      // Si un ID existe dj, c'est une mise  jour plutt qu'une cration
      const operation = competitionId ? "mise  jour" : "cration";
      console.log(`Opration: ${operation} de la comptition`);

      try {
        const savedCompetition = await saveCompetitionState(competitionData);
        console.log(`Comptition ${operation} avec succs:`, savedCompetition);

        // Ne mettre  jour l'ID que s'il n'existe pas dj
        if (!competitionId) {
          setCompetitionId(savedCompetition.id);
        }

        return savedCompetition.id;
      } catch (error) {
        // Si l'erreur est 404 (Not Found) et qu'on essayait de mettre  jour
        // Cela signifie que la comptition n'existe plus dans la base de donnes
        if (competitionId && error.statusCode === 404) {
          console.log(
            "La comptition n'existe plus dans la base de donnes. Cration d'une nouvelle comptition."
          );

          // Rinitialiser l'ID pour crer une nouvelle comptition
          setCompetitionId(null);

          // Recrer sans ID
          const newCompetitionData = {
            ...competitionData,
            id: null, // Forcer la cration d'une nouvelle comptition
          };

          // Nouvelle tentative avec cration plutt que mise  jour
          const savedCompetition = await saveCompetitionState(
            newCompetitionData
          );
          console.log(
            "Nouvelle comptition cre avec succs:",
            savedCompetition
          );

          setCompetitionId(savedCompetition.id);
          return savedCompetition.id;
        } else {
          // Pour les autres types d'erreurs, propager l'erreur
          throw error;
        }
      }
    } catch (error) {
      console.error("Erreur dtaille lors de l'initialisation:", error);

      // Crer un message d'erreur plus dtaill
      let errorMessage = "Erreur lors de l'initialisation de la comptition.";

      if (error.message) {
        errorMessage += `\nDtails: ${error.message}`;
      }

      if (error.cause) {
        errorMessage += `\nCause: ${error.cause}`;
      }

      throw new Error(errorMessage);
    }
  };

  return (
    <CompetitionContext.Provider
      value={{
        competitionId,
        setCompetitionId,
        competitionName,
        setCompetitionName,
        initializeCompetition,
      }}
    >
      {children}
    </CompetitionContext.Provider>
  );
};

export const useCompetition = () => useContext(CompetitionContext);
