// Fonction pour dtecter si l'application est excute dans Electron
const isElectron = () => {
  // Vrification plus robuste pour Electron
  return (
    typeof window !== "undefined" &&
    ((window.process && window.process.type === "renderer") ||
      (window.navigator &&
        window.navigator.userAgent.indexOf("Electron") !== -1))
  );
};

// URLs de l'API - utilise localhost sur votre machine, adresse IP sur le rseau, ou variable d'environnement
const LOCAL_API_URL = "http://localhost:3001/api";
const NETWORK_API_URL = "http://192.168.1.18:3001/api";

// Utiliser NEXT_PUBLIC_API_URL si disponible (dploiement Vercel), sinon utiliser l'URL selon le contexte
const API_URL = process.env.NEXT_PUBLIC_API_URL
  ? process.env.NEXT_PUBLIC_API_URL
  : isElectron()
  ? LOCAL_API_URL
  : NETWORK_API_URL;

export { API_URL }; // Exporter API_URL pour l'utiliser dans d'autres fichiers

// Nouvelle fonction pour rcuprer un match par son numro dans une comptition
export const getMatchByNumber = async (competitionId, matchNumber) => {
  try {
    // Vrifier que les paramtres sont valides
    if (!competitionId) {
      throw new Error("ID de comptition non fourni");
    }

    if (!matchNumber) {
      throw new Error("Numro de match non fourni");
    }

    // Appel  l'API en respectant l'ordre des paramtres
    const response = await fetch(
      `${API_URL}/match/byNumber/${competitionId}/${matchNumber}`,
      {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      }
    );

    if (!response.ok) {
      throw new Error(
        `Erreur lors de la recherche du match #${matchNumber}: ${response.status}`
      );
    }

    const match = await response.json();
    return match;
  } catch (error) {
    console.error(
      `Erreur dtaille lors de la recherche du match #${matchNumber}:`,
      error
    );
    throw error;
  }
};

// Sauvegarde d'une comptition nouvelle ou mise  jour d'une existante
export const saveCompetitionState = async (competitionData) => {
  try {
    console.log("Tentative de sauvegarde de la comptition:", competitionData);
    console.log(
      "poolSize reu:",
      competitionData.poolSize,
      "type:",
      typeof competitionData.poolSize
    );

    // Prparer les donnes de la comptition
    const competitionToSave = {
      name: competitionData.name || "Comptition de Taekwondo",
      date: new Date(),
      startTime: competitionData.startTime,
      roundDuration: competitionData.roundDuration,
      breakDuration: competitionData.breakDuration,
      breakFrequency: competitionData.breakFrequency,
      poolSize: competitionData.poolSize,
    };

    console.log("Donnes prpares pour la sauvegarde:", competitionToSave);
    console.log(
      "poolSize envoy  l'API:",
      competitionToSave.poolSize,
      "type:",
      typeof competitionToSave.poolSize
    );

    // Dterminer si on cre une nouvelle comptition ou on met  jour une existante
    let url = `${API_URL}/competition`;
    let method = "POST";

    // Si un ID est fourni, on fait une mise  jour
    if (competitionData.id) {
      console.log(
        `Mise  jour de la comptition existante avec ID: ${competitionData.id}`
      );
      url = `${API_URL}/competition/${competitionData.id}`;
      method = "PUT";
    } else {
      console.log("Cration d'une nouvelle comptition");
    }

    const response = await fetch(url, {
      method: method,
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(competitionToSave),
    });

    if (!response.ok) {
      // Capturer le code HTTP de l'erreur pour le traiter plus tard
      const statusCode = response.status;
      let errorMessage;

      try {
        const errorData = await response.json();
        errorMessage = errorData.details || `Erreur HTTP: ${statusCode}`;
      } catch (jsonError) {
        // Si le corps de la rponse n'est pas du JSON valide
        errorMessage = `Erreur HTTP: ${statusCode}`;
      }

      // Crer une erreur avec le code de statut pour permettre des traitements spcifiques
      const error = new Error(errorMessage);
      error.statusCode = statusCode;
      throw error;
    }

    const result = await response.json();
    console.log("Comptition sauvegarde avec succs:", result);
    return result;
  } catch (error) {
    console.error(
      "Erreur dtaille lors de la sauvegarde de la comptition:",
      error
    );

    // Propager l'erreur avec le code de statut si disponible
    const enhancedError = new Error(
      "Erreur lors de la sauvegarde de la comptition."
    );
    enhancedError.cause = error;
    enhancedError.statusCode = error.statusCode; // Conserver le code de statut
    throw enhancedError;
  }
};

// Sauvegarde des groupes et poules
// Sauvegarde des groupes et poules - Version corrige
export const saveGroupsAndPools = async (competitionId, groups) => {
  const errors = [];
  const savedGroups = [];
  const participantGroupAssociations = [];

  // Vrification des donnes de base
  if (!competitionId) {
    console.error("ID de comptition manquant");
    throw new Error("ID de comptition requis");
  }

  if (!groups || !Array.isArray(groups) || groups.length === 0) {
    console.error("Aucun groupe  sauvegarder");
    throw new Error("Aucun groupe  sauvegarder");
  }

  console.log(
    `Sauvegarde de ${groups.length} groupes pour la comptition ${competitionId}`
  );
  console.log(
    "Structure du premier groupe:",
    JSON.stringify(groups[0], null, 2)
  );

  // 1re tape: rcuprer tous les participants de la comptition pour vrification
  try {
    console.log(
      `Rcupration des participants pour la comptition ${competitionId}...`
    );
    const participantsResponse = await fetch(
      `${API_URL}/participants?competitionId=${competitionId}`,
      {
        method: "GET",
      }
    );

    if (!participantsResponse.ok) {
      console.warn(
        `Impossible de rcuprer les participants existants: ${participantsResponse.status}`
      );
    } else {
      const existingParticipants = await participantsResponse.json();
      console.log(
        `${existingParticipants.length} participants trouvs dans la base de donnes`
      );

      // Crer un set des IDs pour vrification rapide
      const existingParticipantIds = new Set(
        existingParticipants.map((p) => p.id)
      );

      // Vrifier les IDs des participants dans les groupes
      let totalParticipantsInGroups = 0;
      let validParticipantsInGroups = 0;
      // Garder la trace des participants dj compts pour viter les doublons
      const countedParticipantIds = new Set();

      groups.forEach((group) => {
        if (Array.isArray(group.participants)) {
          totalParticipantsInGroups += group.participants.length;

          group.participants.forEach((participant) => {
            if (
              participant &&
              participant.id &&
              existingParticipantIds.has(participant.id) &&
              !countedParticipantIds.has(participant.id)
            ) {
              validParticipantsInGroups++;
              countedParticipantIds.add(participant.id);
            }
          });
        }

        if (Array.isArray(group.pools)) {
          group.pools.forEach((pool) => {
            if (Array.isArray(pool)) {
              pool.forEach((participantId) => {
                if (
                  participantId &&
                  existingParticipantIds.has(participantId) &&
                  !countedParticipantIds.has(participantId)
                ) {
                  validParticipantsInGroups++;
                  countedParticipantIds.add(participantId);
                } else if (
                  participantId &&
                  !existingParticipantIds.has(participantId)
                ) {
                  console.warn(
                    `Participant ${participantId} dans la poule mais non trouv dans la base de donnes`
                  );
                }
              });
            }
          });
        }
      });

      console.log(
        `Validation des participants: ${validParticipantsInGroups} valides sur ${totalParticipantsInGroups} total`
      );
    }
  } catch (error) {
    console.error("Erreur lors de la vrification des participants:", error);
  }

  for (let group of groups) {
    try {
      // Validation des donnes du groupe
      if (!group.gender) {
        console.error("Genre manquant pour le groupe:", group);
        errors.push("Genre manquant pour un groupe");
        continue;
      }

      if (!group.ageCategory) {
        console.error("Catgorie d'ge manquante pour le groupe:", group);
        errors.push("Catgorie d'ge manquante pour un groupe");
        continue;
      }

      if (!group.weightCategory) {
        console.error("Catgorie de poids manquante pour le groupe:", group);
        errors.push("Catgorie de poids manquante pour un groupe");
        continue;
      }

      // Extraire et vrifier les informations de catgorie d'ge
      let ageCategoryName, ageCategoryMin, ageCategoryMax;

      if (typeof group.ageCategory === "object") {
        ageCategoryName = group.ageCategory.name || "Catgorie inconnue";

        // Vrifier si min et max existent et sont des nombres
        if ("min" in group.ageCategory && !isNaN(group.ageCategory.min)) {
          ageCategoryMin = group.ageCategory.min;
        } else {
          console.log(
            `Min manquant pour ${ageCategoryName}, utilisation de valeur par dfaut 0`
          );
          ageCategoryMin = 0;
        }

        if ("max" in group.ageCategory && !isNaN(group.ageCategory.max)) {
          ageCategoryMax = group.ageCategory.max;
        } else {
          console.log(
            `Max manquant pour ${ageCategoryName}, utilisation de valeur par dfaut 99`
          );
          ageCategoryMax = 99;
        }
      } else if (typeof group.ageCategory === "string") {
        // Si c'est une chane, on utilise comme nom avec valeurs par dfaut
        ageCategoryName = group.ageCategory;
        ageCategoryMin = 0;
        ageCategoryMax = 99;
      } else {
        console.error("Format de catgorie d'ge invalide:", group.ageCategory);
        errors.push("Format de catgorie d'ge invalide");
        continue;
      }

      // Extraire et vrifier les informations de catgorie de poids
      let weightCategoryName, weightCategoryMax;

      if (typeof group.weightCategory === "object") {
        weightCategoryName = group.weightCategory.name || "Catgorie inconnue";

        // Vrifier si max existe et est un nombre
        if ("max" in group.weightCategory && !isNaN(group.weightCategory.max)) {
          weightCategoryMax = group.weightCategory.max;
        } else {
          console.log(
            `Max manquant pour ${weightCategoryName}, utilisation de valeur par dfaut 999`
          );
          weightCategoryMax = 999;
        }
      } else if (typeof group.weightCategory === "string") {
        // Si c'est une chane, on utilise comme nom avec valeur par dfaut
        weightCategoryName = group.weightCategory;
        weightCategoryMax = 999;
      } else {
        console.error(
          "Format de catgorie de poids invalide:",
          group.weightCategory
        );
        errors.push("Format de catgorie de poids invalide");
        continue;
      }

      // Prparation des donnes du groupe au format attendu par l'API
      const groupData = {
        competitionId,
        gender: group.gender,
        ageCategoryName,
        ageCategoryMin,
        ageCategoryMax,
        weightCategoryName,
        weightCategoryMax,
      };

      console.log(
        `Sauvegarde du groupe: ${groupData.gender} - ${groupData.ageCategoryName} - ${groupData.weightCategoryName}`
      );
      console.log("Donnes formates:", groupData);

      const groupResponse = await fetch(`${API_URL}/group`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(groupData),
      });

      if (!groupResponse.ok) {
        const responseText = await groupResponse.text();
        console.error(
          `Erreur lors de la sauvegarde du groupe: ${responseText}`
        );
        errors.push(`Erreur lors de la sauvegarde du groupe: ${responseText}`);
        continue;
      }

      const savedGroup = await groupResponse.json();
      console.log(`Groupe cr avec succs: ${savedGroup.id}`);
      savedGroups.push(savedGroup);

      // Crer les associations participant-groupe si les participants sont disponibles
      if (Array.isArray(group.participants) && group.participants.length > 0) {
        console.log(
          `Association de ${group.participants.length} participants au groupe ${savedGroup.id}`
        );

        for (const participant of group.participants) {
          if (participant && participant.id) {
            try {
              // Vrifier d'abord si le participant existe
              const checkParticipantResponse = await fetch(
                `${API_URL}/participant/${participant.id}`,
                {
                  method: "GET",
                }
              );

              if (!checkParticipantResponse.ok) {
                console.warn(
                  `Participant ${participant.id} non trouv pour le groupe ${savedGroup.id}`
                );
                continue;
              }

              // Crer l'association participant-groupe
              const participantGroupResponse = await fetch(
                `${API_URL}/participantGroup`,
                {
                  method: "POST",
                  headers: {
                    "Content-Type": "application/json",
                  },
                  body: JSON.stringify({
                    participantId: participant.id,
                    groupId: savedGroup.id,
                  }),
                }
              );

              if (!participantGroupResponse.ok) {
                console.warn(
                  `Erreur lors de l'association du participant ${participant.id} au groupe ${savedGroup.id}`
                );
              } else {
                console.log(
                  `Participant ${participant.id} associ au groupe ${savedGroup.id}`
                );
                participantGroupAssociations.push({
                  participantId: participant.id,
                  groupId: savedGroup.id,
                });
              }
            } catch (error) {
              console.error(
                `Erreur lors de l'association du participant ${participant.id} au groupe ${savedGroup.id}:`,
                error
              );
            }
          }
        }
      }

      // Vrification de la prsence des poules
      if (!group.pools || !Array.isArray(group.pools)) {
        console.warn(`Pas de poules pour le groupe ${savedGroup.id}`);
        continue;
      }

      console.log(
        `Sauvegarde de ${group.pools.length} poules pour le groupe ${savedGroup.id}`
      );

      const poolIds = [];
      for (let poolIndex = 0; poolIndex < group.pools.length; poolIndex++) {
        try {
          const poolData = group.pools[poolIndex];

          console.log(
            `Sauvegarde de la poule ${poolIndex + 1} pour le groupe ${
              savedGroup.id
            }`
          );
          const poolResponse = await fetch(`${API_URL}/pool`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              groupId: savedGroup.id,
              poolIndex: poolIndex,
            }),
          });

          if (!poolResponse.ok) {
            const errorText = await poolResponse.text();
            console.error(
              `Erreur lors de la sauvegarde de la poule ${
                poolIndex + 1
              }: ${errorText}`
            );
            errors.push(
              `Erreur lors de la sauvegarde de la poule ${
                poolIndex + 1
              }: ${errorText}`
            );
            continue;
          }

          const savedPool = await poolResponse.json();
          console.log(`Poule cre avec succs: ${savedPool.id}`);
          poolIds.push(savedPool.id);

          if (!Array.isArray(poolData)) {
            console.warn(
              `Les donnes de la poule ${savedPool.id} ne sont pas un tableau`
            );
            continue;
          }

          // Filtrer les participants valides avant de les sauvegarder
          const validParticipants = poolData.filter(
            (participantId) =>
              participantId !== undefined && participantId !== null
          );

          if (validParticipants.length === 0) {
            console.warn(
              `Aucun participant valide pour la poule ${savedPool.id}`
            );
            continue;
          }

          console.log(
            `Sauvegarde de ${validParticipants.length} participants pour la poule ${savedPool.id}`
          );

          for (let participantId of validParticipants) {
            try {
              // Vrifier d'abord si le participant existe
              const checkParticipantResponse = await fetch(
                `${API_URL}/participant/${participantId}`,
                {
                  method: "GET",
                }
              );

              if (!checkParticipantResponse.ok) {
                console.warn(
                  `Participant ${participantId} non trouv, ignor pour la poule ${savedPool.id}`
                );
                errors.push(
                  `Participant ${participantId} non trouv pour la poule ${savedPool.id}`
                );
                continue; // Continuer avec le prochain participant sans arrter l'excution
              }

              // Crer galement l'association participant-groupe si elle n'existe pas dj
              if (
                !participantGroupAssociations.some(
                  (assoc) =>
                    assoc.participantId === participantId &&
                    assoc.groupId === savedGroup.id
                )
              ) {
                const participantGroupResponse = await fetch(
                  `${API_URL}/participantGroup`,
                  {
                    method: "POST",
                    headers: {
                      "Content-Type": "application/json",
                    },
                    body: JSON.stringify({
                      participantId: participantId,
                      groupId: savedGroup.id,
                    }),
                  }
                );

                if (participantGroupResponse.ok) {
                  console.log(
                    `Participant ${participantId} associ au groupe ${savedGroup.id} via la poule`
                  );
                  participantGroupAssociations.push({
                    participantId: participantId,
                    groupId: savedGroup.id,
                  });
                }
              }

              // Le participant existe, on peut l'ajouter  la poule
              const poolParticipantResponse = await fetch(
                `${API_URL}/poolParticipant`,
                {
                  method: "POST",
                  headers: {
                    "Content-Type": "application/json",
                  },
                  body: JSON.stringify({
                    poolId: savedPool.id,
                    participantId,
                  }),
                }
              );

              if (!poolParticipantResponse.ok) {
                const errorText = await poolParticipantResponse.text();
                console.warn(
                  `Erreur lors de l'ajout du participant ${participantId}  la poule ${savedPool.id}: ${errorText}`
                );
                errors.push(
                  `Erreur lors de l'ajout du participant ${participantId}  la poule ${savedPool.id}: ${errorText}`
                );
                // On continue avec le prochain participant
              } else {
                console.log(
                  `Participant ${participantId} ajout  la poule ${savedPool.id}`
                );
              }
            } catch (error) {
              console.error(
                `Erreur lors du traitement du participant ${participantId} pour la poule ${savedPool.id}:`,
                error
              );
              errors.push(
                `Erreur lors du traitement du participant ${participantId} pour la poule ${savedPool.id}: ${error.message}`
              );
              // On continue avec le prochain participant sans arrter l'excution
            }
          }
        } catch (error) {
          console.error(
            `Erreur lors du traitement de la poule ${
              poolIndex + 1
            } pour le groupe ${savedGroup.id}:`,
            error
          );
          errors.push(
            `Erreur lors du traitement de la poule ${
              poolIndex + 1
            } pour le groupe ${savedGroup.id}: ${error.message}`
          );
          // On continue avec la prochaine poule sans arrter l'excution
        }
      }

      // Ajouter les IDs de poules au groupe sauvegard pour rfrence future
      savedGroup.poolIds = poolIds;
    } catch (error) {
      console.error("Erreur lors du traitement du groupe:", error);
      errors.push(`Erreur lors du traitement du groupe: ${error.message}`);
      // On continue avec le prochain groupe sans arrter l'excution
    }
  }

  if (errors.length > 0) {
    console.warn(`Sauvegarde termine avec ${errors.length} erreurs:`, errors);
    return { success: false, errors, savedGroups };
  }

  console.log("Sauvegarde des groupes et poules termine avec succs");
  console.log(
    `Nombre d'associations participant-groupe cres: ${participantGroupAssociations.length}`
  );

  // Identifier les participants qui n'ont pas t placs dans des groupes
  try {
    const placedParticipantIds = new Set(
      participantGroupAssociations.map((assoc) => assoc.participantId)
    );

    // Rcuprer  nouveau tous les participants pour vrifier ceux qui sont placs/non placs
    const allParticipantsResponse = await fetch(
      `${API_URL}/participants?competitionId=${competitionId}`,
      { method: "GET" }
    );

    if (allParticipantsResponse.ok) {
      const allParticipants = await allParticipantsResponse.json();
      const unplacedParticipants = allParticipants.filter(
        (p) => !placedParticipantIds.has(p.id)
      );

      console.log(
        `Nombre de participants non placs dans des groupes: ${unplacedParticipants.length}`
      );

      // Analyser les catgories des groupes existants
      const groupCategories = savedGroups.map((group) => ({
        gender: group.gender,
        ageCategory: group.ageCategoryName,
        weightCategory: group.weightCategoryName,
        ageMin: group.ageCategoryMin,
        ageMax: group.ageCategoryMax,
        weightMax: group.weightCategoryMax,
      }));

      console.log(
        "Catgories disponibles dans les groupes:",
        JSON.stringify(groupCategories, null, 2)
      );

      // Analyser les participants non placs
      if (unplacedParticipants.length > 0) {
        console.log("Dtails des participants non placs:");

        // Regrouper par sexe pour analyse
        const participantsBySexe = {};
        unplacedParticipants.forEach((p) => {
          const sexe = p.sexe || "unknown";
          if (!participantsBySexe[sexe]) {
            participantsBySexe[sexe] = [];
          }
          participantsBySexe[sexe].push(p);

          console.log(
            `ID: ${p.id}, Nom: ${p.nom} ${p.prenom}, Sexe: ${p.sexe}, Age: ${p.age}, Poids: ${p.poids}`
          );
        });

        // Afficher la rpartition par sexe
        console.log("Rpartition des participants non placs par sexe:");
        Object.keys(participantsBySexe).forEach((sexe) => {
          console.log(
            `${sexe}: ${participantsBySexe[sexe].length} participants`
          );
        });

        // Vrifier pour chaque participant non plac pourquoi il ne correspond  aucun groupe
        unplacedParticipants.forEach((p) => {
          const matchingGroups = savedGroups.filter((group) => {
            const matchesSexe = p.sexe === group.gender;
            const matchesAge =
              p.age >= group.ageCategoryMin && p.age <= group.ageCategoryMax;
            const matchesWeight = p.poids <= group.weightCategoryMax;

            return matchesSexe && matchesAge && matchesWeight;
          });

          if (matchingGroups.length === 0) {
            console.log(
              `Participant ${p.id} (${p.nom} ${p.prenom}) ne correspond  aucun groupe existant.`
            );
            console.log(`  Sexe: ${p.sexe}, Age: ${p.age}, Poids: ${p.poids}`);
          } else {
            console.log(
              `Participant ${p.id} (${p.nom} ${p.prenom}) correspond  ${matchingGroups.length} groupe(s) mais n'a pas t plac!`
            );
          }
        });
      }
    }
  } catch (error) {
    console.error(
      "Erreur lors de la vrification des participants non placs:",
      error
    );
  }

  return { success: true, savedGroups };
};

// Sauvegarde d'un match individuel et de ses rsultats
export const saveMatchResult = async (matchId, results) => {
  try {
    console.log("=== Dbut de saveMatchResult ===");
    console.log("Type de matchId:", typeof matchId);
    console.log("Valeur de matchId:", matchId);
    console.log("Rsultats reus:", JSON.stringify(results, null, 2));

    if (!matchId) {
      throw new Error("ID du match non fourni");
    }

    // Rcuprer d'abord les informations du match pour vrifier les positions des participants
    console.log(
      "Rcupration des informations du match avant la mise  jour..."
    );
    const matchInfoResponse = await fetch(`${API_URL}/match/${matchId}`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    });

    if (!matchInfoResponse.ok) {
      throw new Error(
        `Erreur lors de la rcupration des informations du match: ${matchInfoResponse.status}`
      );
    }

    const matchInfo = await matchInfoResponse.json();
    console.log(
      "Informations du match rcupres:",
      JSON.stringify(matchInfo.matchParticipants, null, 2)
    );

    // Identifier les participants par position
    const participantA = matchInfo.matchParticipants.find(
      (mp) => mp.position === "A"
    );
    const participantB = matchInfo.matchParticipants.find(
      (mp) => mp.position === "B"
    );

    if (!participantA || !participantB) {
      console.error(
        "Impossible de trouver les participants A et B dans le match"
      );
      throw new Error("Donnes du match incompltes");
    }

    console.log("Position A:", participantA.participantId);
    console.log("Position B:", participantB.participantId);

    // Dterminer le vainqueur en fonction des rounds gagns par position
    let winnerId = null;
    let winnerPosition = null;

    if (results.completed) {
      // Compter les rounds gagns par chaque position
      let roundsWonByA = 0;
      let roundsWonByB = 0;

      results.rounds.forEach((round) => {
        console.log(
          `Analyse round: A ${round.fighterA || 0} - B ${
            round.fighterB || 0
          }, winner: ${round.winner}`
        );

        // Grer le cas o winner est null mais qu'on peut dterminer un vainqueur  partir des scores
        if (round.winner === null) {
          if ((round.fighterA || 0) > (round.fighterB || 0)) {
            console.log("Dtermination du vainqueur  partir des scores: A");
            roundsWonByA++;
          } else if ((round.fighterB || 0) > (round.fighterA || 0)) {
            console.log("Dtermination du vainqueur  partir des scores: B");
            roundsWonByB++;
          } else {
            console.log("Round nul (0-0 ou galit)");
          }
        } else {
          // Utiliser le vainqueur dj dtermin
          if (round.winner === "A") roundsWonByA++;
          if (round.winner === "B") roundsWonByB++;
        }
      });

      console.log(`Rounds gagns - A: ${roundsWonByA}, B: ${roundsWonByB}`);

      // Dterminer le vainqueur du match
      if (roundsWonByA > roundsWonByB) {
        winnerId = participantA.participantId;
        winnerPosition = "A";
      } else if (roundsWonByB > roundsWonByA) {
        winnerId = participantB.participantId;
        winnerPosition = "B";
      } else {
        // En cas d'galit, on vrifie les scores globaux
        console.log(
          "galit des rounds gagns, vrification des scores globaux..."
        );
        const totalScoreA = results.rounds.reduce(
          (sum, round) => sum + (round.fighterA || 0),
          0
        );
        const totalScoreB = results.rounds.reduce(
          (sum, round) => sum + (round.fighterB || 0),
          0
        );

        console.log(`Score total - A: ${totalScoreA}, B: ${totalScoreB}`);

        if (totalScoreA > totalScoreB) {
          winnerId = participantA.participantId;
          winnerPosition = "A";
          console.log("Vainqueur dtermin par les scores globaux: A");
        } else if (totalScoreB > totalScoreA) {
          winnerId = participantB.participantId;
          winnerPosition = "B";
          console.log("Vainqueur dtermin par les scores globaux: B");
        } else {
          console.log("galit parfaite, pas de vainqueur dtermin");
        }
      }

      console.log(
        `Vainqueur calcul: ${
          winnerPosition || "aucun"
        } (A: ${roundsWonByA} rounds, B: ${roundsWonByB} rounds)`
      );
      console.log(`ID du vainqueur: ${winnerId || "aucun"}`);
    }

    // Prparer les donnes de mise  jour
    console.log("Prparation des donnes de mise  jour...");
    const updateData = {
      status: results.completed ? "completed" : "pending",
      winner: winnerId, // Utiliser l'ID correct du vainqueur
      endTime: results.completed ? new Date().toISOString() : null,
      rounds: results.rounds.map((round, index) => {
        const roundWinnerId =
          round.winner === "A"
            ? participantA.participantId
            : round.winner === "B"
            ? participantB.participantId
            : null;

        return {
          roundNumber: index + 1,
          scoreA: round.fighterA,
          scoreB: round.fighterB,
          winner: roundWinnerId,
          winnerPosition: round.winner,
        };
      }),
    };

    console.log("Donnes de mise  jour:", JSON.stringify(updateData, null, 2));

    // Envoyer la mise  jour au serveur
    console.log("Envoi de la mise  jour au serveur...");
    const url = `${API_URL}/match/${matchId}/results`;
    console.log("URL de la requte:", url);

    const matchResponse = await fetch(url, {
      method: "POST", // Utiliser POST au lieu de PUT pour aller  un endpoint spcifique
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify(updateData),
    });

    console.log("Statut de la rponse:", matchResponse.status);

    if (!matchResponse.ok) {
      let errorMessage = `Erreur HTTP: ${matchResponse.status}`;
      try {
        const errorData = await matchResponse.json();
        errorMessage = errorData.message || errorData.details || errorMessage;
      } catch (e) {
        // Si la rponse n'est pas du JSON valide, on garde le message par dfaut
        console.warn("Impossible de parser la rponse d'erreur comme JSON", e);
      }
      console.error("Erreur lors de la mise  jour:", errorMessage);
      throw new Error(errorMessage);
    }

    const updatedMatch = await matchResponse.json();
    console.log("Match mis  jour avec succs:", updatedMatch);
    return {
      success: true,
      match: updatedMatch,
      winnerPosition: winnerPosition,
    };
  } catch (error) {
    console.error("Erreur dtaille lors de la mise  jour du match:", error);
    return {
      success: false,
      error: error.message,
      details: error,
    };
  }
};

