// Sauvegarde des participants
export const saveParticipants = async (competitionId, participants) => {
  try {
    console.log(
      "Sauvegarde des participants pour la comptition:",
      competitionId
    );
    console.log("Nombre de participants  sauvegarder:", participants.length);

    const savedParticipants = [];

    for (const participant of participants) {
      try {
        console.log("Tentative de sauvegarde du participant:", participant);

        // Vrification et nettoyage des donnes
        let sexe = (participant.sexe || "").toLowerCase().trim();

        // Amlioration de la normalisation du sexe
        if (
          sexe === "m" ||
          sexe === "h" ||
          sexe === "homme" ||
          sexe === "male" ||
          sexe === "masculin"
        ) {
          sexe = "male";
          console.log(
            `Sexe normalis de ${participant.nom} ${participant.prenom}: "${participant.sexe}" -> "male"`
          );
        } else if (
          sexe === "f" ||
          sexe === "femme" ||
          sexe === "female" ||
          sexe === "fminin" ||
          sexe === "feminin"
        ) {
          sexe = "female";
          console.log(
            `Sexe normalis de ${participant.nom} ${participant.prenom}: "${participant.sexe}" -> "female"`
          );
        } else if (
          participant.categorie &&
          typeof participant.categorie === "string"
        ) {
          // Tenter de dterminer le sexe  partir de la catgorie
          const lowerCategorie = participant.categorie.toLowerCase();
          if (
            lowerCategorie.startsWith("female-") ||
            lowerCategorie.startsWith("f-")
          ) {
            sexe = "female";
            console.log(
              `Sexe dtermin par catgorie pour ${participant.nom} ${participant.prenom}: "${participant.categorie}" -> "female"`
            );
          } else if (
            lowerCategorie.startsWith("male-") ||
            lowerCategorie.startsWith("m-")
          ) {
            sexe = "male";
            console.log(
              `Sexe dtermin par catgorie pour ${participant.nom} ${participant.prenom}: "${participant.categorie}" -> "male"`
            );
          } else {
            sexe = "unknown";
            console.warn(
              `Sexe non reconnu pour ${participant.nom} ${participant.prenom}: "${participant.sexe}"`
            );
          }
        } else {
          sexe = "unknown";
          console.warn(
            `Sexe non reconnu pour ${participant.nom} ${participant.prenom}: "${participant.sexe}"`
          );
        }

        const participantData = {
          nom: participant.nom?.trim() || "",
          prenom: participant.prenom?.trim() || "",
          sexe: sexe,
          age: parseInt(participant.age) || 0,
          poids:
            parseFloat(participant.poids?.toString().replace(",", ".")) || 0,
          ligue: participant.ligue?.trim() || "",
          club: participant.club?.trim() || "",
          competitionId: competitionId,
        };

        console.log("Donnes formates du participant:", participantData);

        const response = await fetch(`${API_URL}/participant`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(participantData),
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(
            `Erreur serveur: ${errorData.message || response.statusText}`
          );
        }

        const savedParticipant = await response.json();
        console.log("Participant sauvegard avec succs:", savedParticipant);
        savedParticipants.push(savedParticipant);
      } catch (participantError) {
        console.error(
          `Erreur dtaille pour ${participant.nom} ${participant.prenom}:`,
          participantError
        );
        throw new Error(
          `Erreur lors de la sauvegarde du participant ${participant.nom} ${participant.prenom}: ${participantError.message}`
        );
      }
    }

    console.log(
      "Tous les participants ont t sauvegards avec succs:",
      savedParticipants.length
    );
    return savedParticipants;
  } catch (error) {
    console.error("Erreur lors de la sauvegarde des participants:", error);
    throw error;
  }
};

