// Sauvegarde des matchs gnrs
export const saveGeneratedMatches = async (competitionId, matchesByPool) => {
  const errors = [];
  const savedMatches = [];
  // Garder une trace des combinaisons de participants dj sauvegardes
  const savedCombinations = new Set();

  if (!competitionId) {
    console.error("ID de comptition manquant");
    throw new Error("ID de comptition requis");
  }

  if (
    !matchesByPool ||
    !Array.isArray(matchesByPool) ||
    matchesByPool.length === 0
  ) {
    console.error("Aucun match par poule  sauvegarder");
    throw new Error("Aucun match par poule  sauvegarder");
  }

  console.log(
    `Sauvegarde des matchs pour ${matchesByPool.length} poules dans la comptition ${competitionId}`
  );

  try {
    // Vrifier d'abord s'il existe dj des matchs pour cette comptition
    try {
      const matchesResponse = await fetch(
        `${API_URL}/competition/${competitionId}/matches`,
        {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (matchesResponse.ok) {
        const existingMatches = await matchesResponse.json();

        if (existingMatches && existingMatches.length > 0) {
          console.log(
            `${existingMatches.length} matchs existants trouvs. viter la duplication.`
          );

          // Pr-remplir le Set avec les combinaisons existantes
          for (const match of existingMatches) {
            if (
              match.matchParticipants &&
              match.matchParticipants.length >= 2
            ) {
              const participantIds = match.matchParticipants
                .map((mp) => mp.participant.id)
                .sort();
              if (participantIds.length >= 2) {
                const matchKey = participantIds.join("|");
                savedCombinations.add(matchKey);
              }
            }
          }

          console.log(
            `${savedCombinations.size} combinaisons uniques existantes dtectes`
          );
        }
      }
    } catch (error) {
      console.warn(
        "Erreur lors de la vrification des matchs existants:",
        error
      );
      // Continuer avec la sauvegarde mme si la vrification choue
    }

    // Boucle sur chaque poule
    for (const poolItem of matchesByPool) {
      const { poolId, matches } = poolItem;

      if (!poolId) {
        console.error("ID de poule manquant pour un ensemble de matchs");
        errors.push("ID de poule manquant");
        continue;
      }

      if (!matches || !Array.isArray(matches) || matches.length === 0) {
        console.warn(`Aucun match  sauvegarder pour la poule ${poolId}`);
        continue;
      }

      console.log(
        `Sauvegarde de ${matches.length} matchs pour la poule ${poolId}`
      );

      // Rcuprer les informations de la poule
      const poolResponse = await fetch(`${API_URL}/pool/${poolId}`, {
        method: "GET",
      });

      if (!poolResponse.ok) {
        console.error(
          `Poule ${poolId} non trouve, impossible de sauvegarder les matchs`
        );
        errors.push(`Poule ${poolId} non trouve`);
        continue;
      }

      const pool = await poolResponse.json();

      // Rcuprer les informations du groupe
      const groupResponse = await fetch(`${API_URL}/group/${pool.groupId}`, {
        method: "GET",
      });

      if (!groupResponse.ok) {
        console.error(
          `Groupe ${pool.groupId} non trouv, impossible de sauvegarder les matchs`
        );
        errors.push(`Groupe ${pool.groupId} non trouv`);
        continue;
      }

      const group = await groupResponse.json();

      // Sauvegarder chaque match de la poule
      for (const match of matches) {
        try {
          if (!match.participants || match.participants.length !== 2) {
            console.warn(`Match sans participants valides, ignor`);
            continue;
          }

          // Crer une cl unique pour ce match base sur les IDs des participants
          const participant1 = match.participants[0]?.id || "";
          const participant2 = match.participants[1]?.id || "";
          const matchKey = [participant1, participant2].sort().join("|");

          // Vrifier si cette combinaison a dj t sauvegarde
          if (savedCombinations.has(matchKey)) {
            console.log(
              `Match entre ${participant1} et ${participant2} dj sauvegard, viter le doublon`
            );
            continue;
          }

          // Marquer cette combinaison comme sauvegarde
          savedCombinations.add(matchKey);

          // Trouver le numro du match  partir des informations de schedule
          // Le match.number peut venir du schedule
          let matchNumber = 0;

          if (match.number) {
            // Si le numro est dj dfini dans le match
            matchNumber = match.number;
          } else if (match.id) {
            // Essayer de trouver le numro dans les informations associes au match
          } else {
            // Gnrer un numro bas sur l'index dans la poule
            const index = matches.indexOf(match);
            matchNumber = index + 1 + pool.poolIndex * 100; // Crer un numro unique bas sur l'index de poule
          }

          // Prparer les donnes du match
          const matchData = {
            poolId: poolId,
            groupId: pool.groupId,
            poolIndex: pool.poolIndex,
            matchNumber: matchNumber,
            areaNumber: match.areaNumber || 1, // Utiliser l'aire spcifie ou la valeur par dfaut
            startTime: match.startTime ? new Date(match.startTime) : new Date(),
            status: "pending",
          };

          // Crer le match
          const matchResponse = await fetch(`${API_URL}/match`, {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(matchData),
          });

          if (!matchResponse.ok) {
            const errorText = await matchResponse.text();
            console.error(`Erreur lors de la cration du match: ${errorText}`);
            errors.push(`Erreur lors de la cration du match: ${errorText}`);
            continue;
          }

          const savedMatch = await matchResponse.json();
          savedMatches.push(savedMatch);

          // Associer les participants au match avec une gestion amliore des erreurs
          const participantPromises = [];
          for (let i = 0; i < 2; i++) {
            const participant = match.participants[i];
            const position = i === 0 ? "A" : "B"; // Index 0 correspond  A, Index 1 correspond  B

            if (!participant || !participant.id) {
              console.warn(
                `Participant invalide  la position ${position} pour le match ${savedMatch.id}`
              );
              continue;
            }

            // Vrifier d'abord que le participant existe bien
            const checkParticipantResponse = await fetch(
              `${API_URL}/participant/${participant.id}`,
              {
                method: "GET",
              }
            );

            if (!checkParticipantResponse.ok) {
              console.warn(
                `Participant ${participant.id} non trouv, ignor pour le match ${savedMatch.id}`
              );
              continue;
            }

            // Crer une promesse pour l'ajout du participant au match
            participantPromises.push(
              (async () => {
                try {
                  const matchParticipantResponse = await fetch(
                    `${API_URL}/matchParticipant`,
                    {
                      method: "POST",
                      headers: {
                        "Content-Type": "application/json",
                      },
                      body: JSON.stringify({
                        matchId: savedMatch.id,
                        participantId: participant.id,
                        position: position,
                      }),
                    }
                  );

                  const responseText = await matchParticipantResponse.text();

                  if (!matchParticipantResponse.ok) {
                    console.warn(
                      `Erreur lors de l'ajout du participant ${participant.id} au match ${savedMatch.id}: ${responseText}`
                    );
                    errors.push(
                      `Erreur lors de l'ajout du participant ${participant.id} au match ${savedMatch.id}: ${responseText}`
                    );
                  }
                } catch (error) {
                  console.error(
                    `Erreur lors de l'ajout du participant ${participant.id} au match ${savedMatch.id}:`,
                    error
                  );
                  errors.push(
                    `Erreur lors de l'ajout du participant ${participant.id} au match ${savedMatch.id}: ${error.message}`
                  );
                }
              })()
            );
          }

          // Attendre que toutes les associations de participants soient termines
          await Promise.all(participantPromises);
        } catch (error) {
          console.error(`Erreur lors du traitement d'un match:`, error);
          errors.push(`Erreur lors du traitement d'un match: ${error.message}`);
        }
      }
    }

    console.log(`${savedMatches.length} matchs sauvegards (sans doublons)`);

    if (errors.length > 0) {
      console.warn(
        `Sauvegarde termine avec ${errors.length} erreurs:`,
        errors
      );
      return { success: false, savedMatches, errors };
    }

    console.log("Sauvegarde des matchs termine avec succs");
    return { success: true, savedMatches };
  } catch (error) {
    console.error("Erreur gnrale lors de la sauvegarde des matchs:", error);
    errors.push(`Erreur gnrale: ${error.message}`);
    return { success: false, savedMatches, errors };
  }
};

