/**
 * Vrifie si des rsultats existent pour une comptition
 * @param {string} competitionId - ID de la comptition
 * @returns {Promise<{exists: boolean, count: number}>} - Rsultat de la vrification
 */
export const checkExistingResults = async (competitionId) => {
  try {
    const response = await fetch(`${API_URL}/results/count/${competitionId}`, {
      method: "GET",
    });

    if (!response.ok) {
      throw new Error(`Erreur HTTP: ${response.status}`);
    }

    const data = await response.json();
    return {
      exists: data.count > 0,
      count: data.count,
    };
  } catch (error) {
    console.error(
      "Erreur lors de la vrification des rsultats existants:",
      error
    );
    return { exists: false, count: 0 };
  }
};

/**
 * Charge les rsultats depuis la base de donnes
 * @param {string} competitionId - ID de la comptition
 * @returns {Promise<Object>} - Objet avec les rsultats des matchs
 */
export const loadResults = async (competitionId) => {
  try {
    // Utiliser l'endpoint des matchs avec dtails au lieu de l'endpoint results qui n'existe pas
    const response = await fetch(
      `${API_URL}/competition/${competitionId}/matchesWithDetails`,
      {
        method: "GET",
      }
    );

    if (!response.ok) {
      throw new Error(`Erreur HTTP: ${response.status}`);
    }

    const matches = await response.json();

    // Convertir les donnes des matchs en format de rsultats attendu
    const results = {};

    matches.forEach((match) => {
      if (match.status === "completed" && match.rounds) {
        // Convertir les donnes au format attendu par le front-end
        results[match.id] = {
          completed: true,
          winner:
            match.winnerPosition ||
            (match.winner && match.matchParticipants
              ? match.winner ===
                match.matchParticipants.find((p) => p.position === "A")
                  ?.participantId
                ? "A"
                : match.winner ===
                  match.matchParticipants.find((p) => p.position === "B")
                    ?.participantId
                ? "B"
                : null
              : null),
          rounds: match.rounds.map((round) => ({
            fighterA: round.scoreA,
            fighterB: round.scoreB,
            winner: round.winnerPosition || null,
          })),
        };
      }
    });

    return { data: results };
  } catch (error) {
    console.error("Erreur lors du chargement des rsultats:", error);
    throw error;
  }
};

/**
 * Rcupre toutes les donnes ncessaires pour le calcul des rsultats des poules
 * @param {string} competitionId - ID de la comptition
 * @returns {Promise<Object>} - Toutes les donnes ncessaires
 */
export const fetchResultsData = async (competitionId) => {
  try {
    console.log(
      "Rcupration des donnes de rsultats pour la comptition:",
      competitionId
    );

    // 1. Rcuprer les groupes avec leurs dtails (poules et participants)
    const groupsResponse = await fetch(
      `${API_URL}/competition/${competitionId}/groupsWithDetails`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (!groupsResponse.ok) {
      throw new Error(
        `Erreur lors de la rcupration des groupes: ${groupsResponse.status}`
      );
    }

    const groups = await groupsResponse.json();
    console.log(`${groups.length} groupes rcuprs avec leurs dtails`);

    // 2. Rcuprer tous les matchs, y compris ceux complts
    const matchesResponse = await fetch(
      `${API_URL}/competition/${competitionId}/matchesWithDetails`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (!matchesResponse.ok) {
      throw new Error(
        `Erreur lors de la rcupration des matchs: ${matchesResponse.status}`
      );
    }

    const matches = await matchesResponse.json();
    console.log(`${matches.length} matchs rcuprs au total`);

    // Vrifier les matchs complts
    const completedMatches = matches.filter((m) => m.status === "completed");
    console.log(`${completedMatches.length} matchs complts trouvs`);

    if (completedMatches.length > 0) {
      console.log("Exemple de match complt:", {
        id: completedMatches[0].id,
        matchNumber: completedMatches[0].matchNumber,
        winner: completedMatches[0].winner,
        rounds: completedMatches[0].rounds?.length || 0,
      });
    }

    // 3. Rcuprer tous les participants
    const participantsResponse = await fetch(
      `${API_URL}/participants?competitionId=${competitionId}`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (!participantsResponse.ok) {
      throw new Error(
        `Erreur lors de la rcupration des participants: ${participantsResponse.status}`
      );
    }

    const participants = await participantsResponse.json();
    console.log(`${participants.length} participants rcuprs`);

    // 4. Rcuprer tous les rsultats de matchs sauvegards sparment (si applicable)
    // Dans certains cas, les rsultats peuvent tre stocks dans une collection spare
    let matchResults = {};

    try {
      const resultsResponse = await fetch(
        `${API_URL}/match/results/${competitionId}`,
        {
          method: "GET",
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (resultsResponse.ok) {
        const results = await resultsResponse.json();
        // Organiser les rsultats par ID de match
        results.forEach((result) => {
          matchResults[result.matchId] = result;
        });
        console.log(
          `${
            Object.keys(matchResults).length
          } rsultats de matchs rcuprs depuis la collection de rsultats`
        );
      } else {
        console.log(
          "Pas de collection de rsultats spare disponible. Utilisation des donnes intgres aux matchs."
        );
      }
    } catch (error) {
      console.log(
        "Pas de collection de rsultats spare disponible:",
        error.message
      );
    }

    // Si nous n'avons pas de rsultats spars, crer les rsultats  partir des donnes de matchs
    if (Object.keys(matchResults).length === 0 && completedMatches.length > 0) {
      console.log(
        "Cration des rsultats  partir des donnes de matchs complts"
      );

      completedMatches.forEach((match) => {
        // Crer un objet de rsultat bas sur les donnes du match
        matchResults[match.id] = {
          matchId: match.id,
          winner: match.winner,
          completed: true,
          rounds: match.rounds || [],
        };
      });

      console.log(
        `${
          Object.keys(matchResults).length
        } rsultats crs  partir des matchs complts`
      );
    }

    return {
      participants,
      groups,
      matches,
      matchResults,
    };
  } catch (error) {
    console.error("Erreur lors de la rcupration des donnes:", error);
    throw error;
  }
};

/**
 * Supprime tous les groupes d'une comptition
 * @param {string} competitionId - ID de la comptition
 * @returns {Promise<{success: boolean, message: string}>} - Rsultat de l'opration
 */
export const deleteAllGroups = async (competitionId) => {
  try {
    console.log(
      `Suppression de tous les groupes pour la comptition ${competitionId}`
    );

    // Vrifier d'abord que la comptition existe
    const competitionResponse = await fetch(
      `${API_URL}/competition/${competitionId}`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (!competitionResponse.ok) {
      throw new Error(`Comptition non trouve avec l'ID: ${competitionId}`);
    }

    // Rcuprer tous les groupes existants pour cette comptition
    const groupsResponse = await fetch(
      `${API_URL}/competition/${competitionId}/groups`,
      {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
        },
      }
    );

    if (!groupsResponse.ok) {
      throw new Error(
        `Erreur lors de la rcupration des groupes: ${groupsResponse.status}`
      );
    }

    const groups = await groupsResponse.json();

    if (groups.length === 0) {
      return { success: true, message: "Aucun groupe  supprimer" };
    }

    console.log(`${groups.length} groupes  supprimer`);

    // Supprimer chaque groupe
    const deletionPromises = groups.map(async (group) => {
      try {
        const deleteResponse = await fetch(`${API_URL}/group/${group.id}`, {
          method: "DELETE",
          headers: {
            "Content-Type": "application/json",
          },
        });

        if (!deleteResponse.ok) {
          console.warn(
            `Erreur lors de la suppression du groupe ${group.id}: ${deleteResponse.status}`
          );
          return { success: false, groupId: group.id };
        }

        return { success: true, groupId: group.id };
      } catch (error) {
        console.error(
          `Erreur lors de la suppression du groupe ${group.id}:`,
          error
        );
        return { success: false, groupId: group.id, error: error.message };
      }
    });

    const results = await Promise.all(deletionPromises);
    const successful = results.filter((r) => r.success).length;
    const failed = results.filter((r) => !r.success).length;

    return {
      success: failed === 0,
      message: `${successful} groupes supprims avec succs, ${failed} checs`,
      details: {
        total: groups.length,
        successful,
        failed,
        failedGroupIds: results.filter((r) => !r.success).map((r) => r.groupId),
      },
    };
  } catch (error) {
    console.error("Erreur lors de la suppression des groupes:", error);
    return { success: false, message: error.message };
  }
};
