import React from "react";

const Sidebar = ({ currentStep, setCurrentStep, steps, competitionName }) => {
  // Vrifier si une tape peut tre accessible
  const isStepAccessible = (stepIndex) => {
    // L'tape 0 (liste des comptitions) est toujours accessible
    if (stepIndex === 0) return true;

    // Toutes les tapes sont accessibles une fois qu'on est dans l'application
    // Cela permet de naviguer librement entre les onglets pour les comptitions existantes
    return true;
  };

  // Grer le clic sur une tape
  const handleStepClick = (stepIndex) => {
    if (isStepAccessible(stepIndex)) {
      setCurrentStep(stepIndex);
    }
  };

  return (
    <div className="sidebar">
      <div className="sidebar-title">
        {competitionName || "Comptition de Taekwondo"}
      </div>
      <ul className="sidebar-steps">
        {steps.map((step, index) => (
          <li
            key={index}
            className={`sidebar-step ${currentStep === index ? "active" : ""} ${
              !isStepAccessible(index) ? "disabled" : ""
            }`}
            onClick={() => handleStepClick(index)}
          >
            <div className="sidebar-step-number">{index}</div>
            <div className="sidebar-step-label">{step}</div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Sidebar;
